import Vue from "vue";
import vuex from "vuex";

Vue.use(vuex);

// 导出vuex仓库
export default new vuex.Store({
  state: {
    // 初始值(响应值)
    inputValue: "",
    // 未完成的事情
    notFinishedArr: [
      {
        id: 1,
        thing: "敲代码",
        isChecked: false,
      },
      {
        id: 2,
        thing: "整理笔记",
        isChecked: false,
      },
    ],
    //  已完成的事情
    finishedArr: [
      {
        id: 3,
        thing: "脑袋放空, 进入提前放假模式",
        isChecked: true,
      },
    ],
  },
  mutations: {
    // input框输入内容处理函数
    inputHandle(state, payload) {
      // 将input的值赋给响应值
      state.inputValue = payload.target.value;
      // 添加到未完成事件的数组中
      state.notFinishedArr.push({
        id: Date.now(),
        thing: state.inputValue,
        isChecked: false,
      });
      // 清空input值
      state.inputValue = "";
    },
    // 未完成的事件
    changeHandle(state, { item, index }) {
      item.isChecked = !item.isChecked;
      // 如果isChecked为true, 就意味着已完成
      if (item.isChecked) {
        // 推进已完成的数组中
        state.finishedArr.push(item);
        // 删除当前在未完成的数组对应成员
        state.notFinishedArr.splice(index, 1);
      } else {
        // 推进未完成的数组中
        state.notFinishedArr.push(item);
        // 删除当前在已完成的数组对应成员
        state.finishedArr.splice(index, 1);
      }
    },
  },
});
