<template>
  <div id="app">
    <h1>TodoList</h1>
    <!-- 用户输入内容 -->
    <input type="text" @keydown.enter="inputHandle" :value="inputValue" />
    <!-- 未完成 -->
    <h1>未完成事件</h1>
    <div class="not-finished">
      <todo-list
        :props-arr="notFinishedArr"
        @com-event="changeHandle"
      ></todo-list>
    </div>

    <!-- 已经完成 -->
    <h1>已经完成事件</h1>
    <div class="finished">
      <todo-list :props-arr="finishedArr" @com-event="changeHandle"></todo-list>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  name: "App",
  components: {},
  //获取vuex中的数据
  computed: {
    ...mapState(["notFinishedArr", "finishedArr", "inputValue"]),
  },
  methods: { ...mapMutations(["inputHandle", "changeHandle"]) },
};
</script>

